@font-face {
  font-family: "Rubik";
  font-weight: normal;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/rubik-regular.woff2") format("woff2"), url("../fonts/rubik-regular.woff") format("woff");
}
@font-face {
  font-family: "Rubik";
  font-weight: bold;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/rubik-medium.woff2") format("woff2"), url("../fonts/rubik-medium.woff") format("woff");
}
@font-face {
  font-family: "Rubik";
  font-weight: bold;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/rubik-bold.woff2") format("woff2"), url("../fonts/rubik-bold.woff") format("woff");
}
* {
  box-sizing: border-box;
}
html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}
html {
  font-style: normal;
  font-weight: 400;
  font-family: "Rubik", "Arial", sans-serif;
  color: #000000;
}
img {
  max-width: 100%;
  height: auto;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.container {
  max-width: 1570px;
  margin: 0 auto;
  padding: 80px 86px;
}
@media (max-width: 1023px) {
  .container {
    padding: 40px 46px;
  }
}
@media (max-width: 768px) {
  .container {
    padding: 40px 20px;
  }
}
.card {
  position: relative;
  display: flex;
  flex-direction: column;
  background: #FFFFFF;
  height: 100%;
  border: 1px solid #ECF0F4;
  border-radius: 5px;
  overflow: hidden;
  cursor: pointer;
  transition: border-color 0.3s ease;
}
.card:hover {
  border-color: #535658;
}
.card__photos {
  position: relative;
  background-color: #ECF0F4;
  width: 100%;
  height: 334px;
}
@media (max-width: 767px) {
  .card__photos {
    height: 260px;
  }
}
.card__image {
  display: none;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.card__image.active {
  display: block;
}
.card__image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.card__photos-text {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -30px);
  font-size: 19px;
  line-height: 22px;
  color: #8D9295;
  text-transform: uppercase;
}
.card__pagination {
  position: absolute;
  left: 50%;
  bottom: 15px;
  transform: translateX(-50%);
  z-index: 10;
}
.card__pagination-btn {
  position: relative;
  border: none;
  padding: 0;
  width: 20px;
  height: 2px;
  background-color: #000000;
  opacity: 0.2;
  margin-right: 4px;
  transition: background-color 0.3s ease, opacity 0.3s ease;
  cursor: pointer;
}
.card__pagination-btn::after {
  content: "";
  position: absolute;
  top: -10px;
  left: 0;
  width: 20px;
  height: 20px;
}
.card__pagination-btn:last-of-type {
  margin: 0;
}
.card__pagination-btn.active {
  background-color: #F1D264;
  opacity: 1;
}
.card__pagination-btn:hover {
  background-color: #F1D264;
  opacity: 1;
}
.card__buttons {
  position: absolute;
  top: 20px;
  right: 20px;
  display: flex;
  flex-direction: column;
}
.card__button {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 34px;
  height: 34px;
  padding: 0;
  background-color: #FFFFFF;
  border-radius: 50%;
  overflow: hidden;
  border: none;
  margin-bottom: 5px;
  transition: background-color 0.3s ease;
  cursor: pointer;
}
.card__button:last-child {
  margin-bottom: 0;
}
.card__button:hover {
  background-color: #ECF0F4;
}
.card__tag {
  position: absolute;
  top: 20px;
  left: 20px;
  width: 50px;
  height: 50px;
}
.card__tag--hit svg {
  fill: #DB3737;
}
.card__tag--new svg {
  fill: #5697D3;
}
.card__tag-text {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 14px;
  line-height: 20px;
  font-weight: 700;
  color: #FFFFFF;
  text-transform: uppercase;
}
.card__info {
  display: flex;
  flex: 1;
  flex-direction: column;
  padding: 10px 25px 25px;
}
@media (max-width: 767px) {
  .card__info {
    padding: 10px 20px 20px;
  }
}
.card__name {
  font-size: 14px;
  line-height: 20px;
  font-weight: 400;
  margin: 0 0 5px;
  text-align: center;
  flex: 1;
}
@media (max-width: 767px) {
  .card__name {
    font-size: 16px;
    line-height: 22px;
    margin: 0 0 15px;
  }
}
.card__price {
  display: flex;
  justify-content: center;
  font-size: 20px;
  line-height: 22px;
  font-weight: 700;
  margin: 0 0 10px;
}
.card__price--old {
  font-weight: 400;
  font-size: 14px;
  line-height: 22px;
  text-decoration-line: line-through;
  color: #8D9295;
  margin-left: 10px;
}
@media (max-width: 767px) {
  .card__price {
    margin: 0 0 20px;
  }
  .card__price--old {
    margin-left: 16px;
  }
}
.card__bottom {
  display: flex;
}
.card__count {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100px;
  height: 50px;
  padding: 10px 5px;
  margin-right: 10px;
  border: 1px solid #ECF0F4;
  border-radius: 5px;
}
@media (max-width: 767px) {
  .card__count {
    display: none;
  }
}
.card__count-btn {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 30px;
  height: 30px;
  border: none;
  background-color: #FFFFFF;
  padding: 0;
  cursor: pointer;
}
.card__count-btn::after {
  position: absolute;
  content: "";
  width: 12px;
  height: 2px;
  left: 9px;
  top: 14px;
  background-color: #8D9295;
  transition: background-color 0.3s ease;
}
.card__count-btn--plus::before {
  position: absolute;
  content: "";
  width: 2px;
  height: 12px;
  left: 14px;
  top: 9px;
  background-color: #8D9295;
  transition: background-color 0.3s ease;
}
.card__count-btn:hover::after,
.card__count-btn:hover::before {
  background-color: #000000;
}
.card__count-input {
  width: 30px;
  height: 30px;
  border: none;
  background-color: #FFFFFF;
  text-align: center;
  padding: 0;
  font-size: 18px;
  line-height: 22px;
}
.card__count-input::-webkit-outer-spin-button,
.card__count-input::-webkit-inner-spin-button {
  -webkit-appearance: none;
}
.card__count-input,
.card__count-input:hover,
.card__count-input:focus {
  appearance: none;
  -moz-appearance: textfield;
}
.card__buy-btn {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 20px;
  height: 50px;
  background: #F1D264;
  color: #000000;
  border-radius: 5px;
  border: none;
  width: 100%;
  transition: background-color 0.3s ease;
  cursor: pointer;
}
.card__buy-btn:hover {
  background-color: #ECF0F4;
}
.card__buy-text {
  margin-left: 12px;
  font-size: 14px;
  line-height: 17px;
  font-weight: 500;
}
.tabs__wrapper {
  overflow: hidden;
}
.tabs__nav {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 40px;
}
.tabs__nav::-webkit-scrollbar {
  display: none;
}
@media (max-width: 768px) {
  .tabs__nav {
    margin-bottom: 17px;
  }
}
.tabs__button {
  padding: 0;
  margin-right: 25px;
  font-size: 24px;
  line-height: 34px;
  font-weight: 400;
  color: #535658;
  border: none;
  border-bottom: 1px dashed #535658;
  background-color: transparent;
  transition: opacity 0.3s ease;
  cursor: pointer;
}
.tabs__button:last-child {
  margin-right: 0;
}
.tabs__button--active {
  font-size: 34px;
  line-height: 40px;
  font-weight: 700;
  text-transform: uppercase;
  color: #000000;
  border: none;
}
.tabs__button:hover {
  opacity: 0.6;
}
@media (max-width: 768px) {
  .tabs__button {
    font-size: 14px;
    line-height: 22px;
    margin-bottom: 7px;
    text-align: start;
  }
  .tabs__button--active {
    font-size: 18px;
    line-height: 26px;
    margin-bottom: 5px;
  }
}
.tabs .tabs__item {
  display: none;
}
.tabs .tabs__item--active {
  display: block;
}
.slider {
  position: relative;
  display: flex;
  width: 100%;
}
.slider__button {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  display: flex;
  justify-content: center;
  align-items: center;
  width: 40px;
  height: 40px;
  padding: 0;
  border: 1px solid #ECF0F4;
  border-radius: 5px;
  background-color: transparent;
  transition: opacity 0.3s ease;
  cursor: pointer;
}
.slider__button--prev {
  left: -86px;
}
.slider__button--next {
  right: -86px;
}
.slider__button--next svg {
  transform: rotate(180deg);
}
.slider__button:hover {
  opacity: 0.7;
}
@media (max-width: 1023px) {
  .slider__button--prev {
    left: -46px;
  }
  .slider__button--next {
    right: -46px;
  }
}
@media (max-width: 767px) {
  .slider__button {
    display: none;
  }
}
.slider__wrapper {
  width: 100%;
  overflow: hidden;
}
.slider__list {
  display: flex;
  flex-wrap: nowrap;
  padding: 0;
  margin: 0;
  list-style: none;
}
.slider__item {
  min-width: 334px;
  min-height: 496px;
  margin-right: 20px;
}
.slider__item:last-child {
  margin: 0;
}
@media (max-width: 767px) {
  .slider__item {
    min-width: 260px;
    min-height: 441px;
  }
}
